.loaderCover{
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.7;
    left: 0;
    top: 0;
    background-color: rgb(255, 255, 255);
    z-index: 100;
}

.loaderImg {
    position: absolute;
    left: 50%;
    top: 30%;
    width: 50px;
    height: 50px;
    z-index: 101;
}